<template>
  <div class="draggable-warp">
    <vue-draggable-resizable :draggable="draggable" class="item" :w="800" :h="400" :parent="true">
      <div class="selector">
        <vue-draggable-resizable @deactivated="onDeactivated" class="item" :w="50" :h="50"  :onDragStart="onDragStartCallback">
        </vue-draggable-resizable>
      </div>
    </vue-draggable-resizable>
  </div>
</template>

<script>
import VueDraggableResizable from 'vue-draggable-resizable'
import 'vue-draggable-resizable/dist/VueDraggableResizable.css'
export default {
  name: 'draggable-div',
  components: {VueDraggableResizable},
  data () {
    return {
      width: 0,
      height: 0,
      x: 0,
      y: 0,
      draggable: true
    }
  },
  methods: {
    onDeactivated () {
      this.draggable = true
    },
    onDragStartCallback () {
      this.draggable = false
      return
    }
  }
}
</script>
<style>
  .draggable-warp {
    position: relative;
    width: 100%;
    height: 100%;
  }
  .draggable-warp  .item{
    display: inset-block;
    border:1px solid red;
  }
</style>